import { Fragment } from "react";
import {generatePrivateKey, getPublicKey} from 'nostr-tools'

export default function Raw() {

let pk = getPublicKey("1b260abeef51e16175954b5fcee77b55aa990360d29e0e94afc68e0dcd45aeae")

    
    return <>
        <Fragment>
            <div style={{ color: "white", marginLeft:'9.2rem' }}>
                {pk}
            </div>
        </Fragment>
    </>
}